export interface IMortgage {
    rate: number;
    points: number;
    term: number;
    loanAmount: number;
    loanBaseAmount: number;

    /**
     * principal interest tax insurance
     */
    piti: number;
    pAndI: number;
}

const getMoney = (value: any) => {
    value = value || 0;
    return (Math.round(parseFloat(value) * 100)) / 100;
}

export class Mortgage implements IMortgage {
    rate: number;
    points: number;
    term: number;
    loanAmount: number;
    piti: number;
    pAndI: number;
    loanBaseAmount: number;
    constructor() {
        this.rate = 0;
        this.points = 0;
        this.term = 0;
        this.loanAmount = 0;
        this.loanBaseAmount = 0;
        this.piti = 0;
        this.pAndI = 0;
    }

}
export interface ICredits {
    credit1Name: string;
    credit1Amount: number;
    credit2Name: string;
    credit2Amount: number;
    credit3Name: string;
    credit3Amount: number;
    credit4Name: string;
    credit4Amount: number;
    totalCredits: number;
}
export class Credits implements ICredits {
    credit1Name: string;
    credit1Amount: number;
    credit2Name: string;
    credit2Amount: number;
    credit3Name: string;
    credit3Amount: number;
    credit4Name: string;
    credit4Amount: number;
    totalCredits: number;
    constructor() {
        this.credit1Name = '';
        this.credit1Amount = null;
        this.credit2Name = '';
        this.credit2Amount = null;
        this.credit3Name = '';
        this.credit3Amount = null;
        this.credit4Name = '';
        this.credit4Amount = null;
    }

}
export interface IPrepaids {
    monthsOfTax: number;
    monthsOfTaxAmount: number;
    monthsOfInsurance: number;
    monthsOfInsuranceAmount: number;
    daysOfInterest: number;
    daysOfInterestAmount: number;
    totalPrePaids: number;
}
export class PrePaids implements IPrepaids {
    monthsOfTax: number;
    monthsOfTaxAmount: number;
    monthsOfInsurance: number;
    monthsOfInsuranceAmount: number;
    daysOfInterest: number;
    daysOfInterestAmount: number;
    totalPrePaids: number;
    constructor() {
        this.monthsOfTax = 0;
        this.monthsOfInsurance = 0;
        this.daysOfInterest = 0;

    }

}
