export interface IClosingCostType {
    id: number;
    name: string;
    calcOn: string;
    isTitleInsurance: boolean;

}
export class ClosingCostType implements IClosingCostType {
    id: number;
    name: string;
    calcOn: string;
    isTitleInsurance: boolean;

}
export interface IClosingCostTemplate {
    id: number;
    name: string;
    stateId: string;
}
export class ClosingCostTemplate implements IClosingCostTemplate {
    id: number;
    name: string;
    stateId: string;
    constructor() {
        this.id = 0;
        this.name = '';
        this.stateId = '';
    }

}

export interface IClosingCostTemplateModel extends IClosingCostTemplate {
    closingCosts: IClosingCost[];
}
export class ClosingCostTemplateModel implements IClosingCostTemplateModel {
    closingCosts: IClosingCost[];
    id: number;
    name: string;
    stateId: string;
    constructor() {
        this.id = 0;
        this.name = '';
        this.stateId = '';
        this.closingCosts = [];
    }

}

export interface IClosingCost {
    /**
     * no from 1-15
     */
    rowNo: number;
    /**
     * 1:Buyer
     * 2:Seller
     * 3:N/A
     */
    type: number;
    name: string;
    percentage?: number;
    value?: number;
    /**
    * 1:[both value and percentage]
    * 2:[value only]
    */
    mode: number;
    include: boolean;
    isApr: boolean;
    isVisible: boolean;
    isPpe?: boolean;
    addToLoan?: boolean;
    templateId?: number;
    closingCostTypeId?: number;
    calcOn?: string;
    isTitleInsurance?: boolean;
}
export interface IModalResponse {
    close: boolean;
    value: any;
}
export class ClosingCost implements IClosingCost {
    isVisible: boolean;
    isApr: boolean;
    rowNo: number;
    include: boolean;
    /**
     * 1:[both value and percentage]
     * 2:[value only]
     */
    mode: number;
    type: number;
    name: string;
    percentage?: number;
    value?: number;
    /**
     *
     */
    isPpe?: boolean;
    addToLoan?: boolean;
    templateId?: number;
    closingCostTypeId?: number;
    calcOn?: string;
    isTitleInsurance?: boolean;
    constructor(param?: IClosingCost) {
        this.isVisible = true;
        if (param) {
            this.name = param.name || '';
            this.type = param.type || 1;
            this.value = param.value || null;
            this.percentage = param.percentage || null;
            this.mode = param.mode || 2;
            this.rowNo = param.rowNo;
            this.isApr = param.isApr;
            this.isPpe = param.isPpe;
            this.addToLoan = param.addToLoan;
            this.templateId = param.templateId;
            this.calcOn = param.calcOn;
            this.isTitleInsurance = param.isTitleInsurance;
        }
        else {
            this.name = '';
            this.type = 1;
            // this.value = 0;
            this.percentage = null;
            this.mode = 2;
            this.value = null;
            this.isApr = false;
            this.isPpe = false;
            this.addToLoan = false;
            this.closingCostTypeId = 0;
            this.calcOn = '';
            this.isTitleInsurance = false;

        }
    }
}



