import { Component, Input, ViewChild, OnInit } from '@angular/core';

import { LoanOption, IClosingCost, ClosingCost, IClosingCostType, IClosingCostTemplateModel } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BsModalRef } from "ngx-bootstrap/modal";
import { SettingsService } from "app/routes/loan/shared/settings.service";
import { STATE_LIST } from 'app/routes/loan/shared/models/state-list.model';
import { forEach } from '@angular/router/src/utils/collection';
@Component({
    selector: 'app-closing-costs-template',
    templateUrl: './closing-costs-template.component.html',
    styleUrls: ['./closing-costs-template.component.scss']
})
export class ClosingCostsTemplateComponent implements OnInit {
    @Input()
    closingCostTemplate: IClosingCostTemplateModel;
    closingCostTypes: IClosingCostType[] = [];
    closingCostFilteredTypes: IClosingCostType[] = [];
    isTitleInsuranceAdded: boolean;
    stateList = STATE_LIST;

    constructor(public bsModalRef: BsModalRef,
        private settingsService: SettingsService
    ) {


    }
    ngOnInit() {
        this.settingsService.getClosingCostsType().subscribe((cTypes) => {
            this.closingCostTypes = Object.assign(this.closingCostTypes, cTypes);
            this.closingCostFilteredTypes = this.closingCostTypes.filter(x => x.isTitleInsurance != true);
        });
        this.isTitleInsuranceAdded = this.closingCostTemplate.closingCosts.filter(x => x.isTitleInsurance).length > 0;
    }
    saveClosingCosts() {
        // this.settingsService.saveClosingCosts(this.closingCosts).subscribe((resp) => {
        //     console.log('saveClosingCosts success', this.closingCosts, resp);
        //     this.bsModalRef.hide();
        // });
    }
    private stateChange(stateId: string) {
        console.log(stateId);
    }
    private removeClosingCost(closingCost: IClosingCost, index: number) {
        this.closingCostTemplate.closingCosts.splice(index, 1);
        this.closingCostTemplate.closingCosts.forEach((item, index) => {
            item.rowNo = index + 1;
        });
    }
    private closingCostTypeChange(closingCostTypeId: number, index: number) {
        const closingCostType = this.closingCostTypes.filter(x => x.id === closingCostTypeId)[0];
        const cType = { calcOn: closingCostType.calcOn, name: closingCostType.name, isTitleInsurance: closingCostType.isTitleInsurance };
        this.closingCostTemplate.closingCosts[index] = { ...this.closingCostTemplate.closingCosts[index], ...cType };
        this.isTitleInsuranceAdded = this.closingCostTemplate.closingCosts.filter(x => x.isTitleInsurance == true).length > 0;
    }

    private addClosingCost() {
        let cc = new ClosingCost();
        cc.rowNo = this.closingCostTemplate.closingCosts.length + 1;
        this.closingCostTemplate.closingCosts.push(cc);
    }
}



