import { Component, Input, ViewChild, OnInit } from '@angular/core';

import { LoanOption, IClosingCost, ClosingCost, IClosingCostType, ClosingCostTemplateModel, IModalResponse } from './../shared/models/index';
import { HelperService } from 'app/routes/loan/shared/helper.service';
import { BsModalRef } from "ngx-bootstrap/modal";
import { SettingsService } from "app/routes/loan/shared/settings.service";
import { IClosingCostTemplateModel } from 'app/routes/loan/shared/models/loan-option.model';
import { Subject } from 'rxjs';
@Component({
    selector: 'app-closing-costs-template-container',
    templateUrl: './closing-costs-template-container.component.html',
    styleUrls: ['./closing-costs-template-container.component.scss']
})
export class ClosingCostsTemplateContainerComponent implements OnInit {
    // @Input() loanOptionDetails: LoanOption;
    closingCostTypes: IClosingCostType[] = [];
    closingCostTemplates: IClosingCostTemplateModel[] = [];
    closingCostTemplateId?: any;

    closingCostTemplate: IClosingCostTemplateModel = new ClosingCostTemplateModel();
    public onClose: Subject<IModalResponse>;

    constructor(public bsModalRef: BsModalRef,
        private settingsService: SettingsService
    ) {
        this.closingCostTemplateId = 0;


    }
    ngOnInit() {
        // this.settingsService.getClosingCostsType().subscribe((cTypes) => {
        //     this.closingCostTypes = Object.assign(this.closingCostTypes, cTypes);
        // });
        this.settingsService.getClosingCostsTemplates().subscribe((cTypes) => {
            this.closingCostTemplates = Object.assign(this.closingCostTemplates, cTypes);
        });
        this.onClose = new Subject();
    }
    private templateChange(templateId: number) {
        console.log(templateId);
        this.closingCostTemplate = this.closingCostTemplates.filter(x => +x.id === +templateId)[0];
    }
    saveClosingCostsTemplates() {
        console.log('this.closingCostTemplate', this.closingCostTemplate);
        this.settingsService.saveClosingCostsTemplates(this.closingCostTemplate).subscribe((resp: any) => {
            console.log('saveClosingCosts success', this.closingCostTemplate, resp);

            this.settingsService.getClosingCostsTemplates().subscribe((cTypes) => {
                this.closingCostTemplates = Object.assign(this.closingCostTemplates, cTypes);
                this.closingCostTemplateId = String(resp.data);
                this.templateChange(resp.data);
            });
            // this.bsModalRef.hide();
        });
    }
    private useThisClosingCostTemplate() {
        const val: IModalResponse = { close: true, value: this.closingCostTemplate };
        this.onClose.next(val);
        this.bsModalRef.hide();
    }
    private closeModal() {
        const val: IModalResponse = { close: false, value: '' };
        this.onClose.next(val);
        this.bsModalRef.hide();
    }
    private addNewClosingCostTemplate() {
        const newTemplate = new ClosingCostTemplateModel();
        this.closingCostTemplateId = null;
        this.closingCostTemplates.push(newTemplate);
        this.closingCostTemplate = newTemplate;
    }

}



